<template>
	<div class=top_wrapper>
		<div class="top_bar">
			<a @click="back" class="css_l_arrow"></a>
			<span class="ellipsis1">{{theme}}</span>
		</div>
	</div>
</template>
<script>
	export default {
		props:{
			theme:String
		},
		methods:{
			back (){
				this.$router.back()
			}
		}
	}
</script>
<style scoped>
.top_wrapper{
	height: 0.45rem;
}
.top_bar{
     height: 0.45rem;
     background-color: #e6292c;
     padding:0rem 0.5rem;
     box-shadow: 0 0.02rem 0.04rem 0 rgba(0,0,0,.1);
     text-align: center;
     position: fixed;
     color: #fff;
     line-height: 0.45rem;
     width: 100%;
     left:0px;
     top: 0px;
     z-index: 99;
     box-sizing: border-box;
}
/* .top_bar .arrow_back{
     background: url(../assets/img/arrow_back.png) no-repeat center center;
     background-size: 0.22rem;
     display: block;
     width: 0.45rem;
     height: 0.45rem;
}*/
/* .top_bar a{
 	position: absolute;
 	left: 0.1rem;
}	*/
 .css_l_arrow{
     width: 0.1rem;
     height: 0.1rem;
     border-top: 0.02rem solid #fff;
     border-left: 0.02rem solid #fff;
     -webkit-transform:rotate(-45deg);
     transform:rotate(-45deg);
     position: absolute;
     top:0.17rem;
     left: 0.2rem;
}
</style>


